@import url(https://fonts.googleapis.com/css?family=Slabo+27px|Roboto|Source+Sans+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


html {
  scroll-behavior: smooth;
}
  .navigation {
    position: absolute;
    width: 100%;
    padding: 10px 0px;
    background: linear-gradient(rgba(10, 10, 10, 0.1), rgba(10, 10, 10, 0.1));
  }
  
  
  .primary-nav li {
    display: inline-block;
    margin: 0px 20px;
    font-size: 15pt;
    color: white;
    padding-bottom: 5px;
    transition: border 50ms linear;
  }
  .primary-nav li:hover {
    text-decoration: none;
    font-weight:bold;
    cursor: pointer;
    color: white;
  }
  
  .main {
    background: linear-gradient(rgba(250, 250, 250, 0.1), rgba(250, 246, 250, 0.1)),
      url(/portfolio/static/media/home.983cbc9a.jpg); 
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    padding: 14.7% 0; 
    color: white;
  }
  
  .entry-banner h1 {
    font-family: "Source Sans Pro", Arial, sans-serif;
  }
  #hello {
    font-size: 28pt;
  }
  #name {
    font-size: 100pt;
  }
  #person-description {
    font-size: 33pt;
  }
  .banner-buttons .btn-border,
  .submit-contact .btn-border,
  .projects .btn-border {
    background-color: transparent;
    min-width: 100%;
    margin-top: 40px;
    color: white;
    font-family: "Roboto", Helvetica, serif;
    font-size: 1.2em;
    border-radius: 0;
    border: 2px solid white;
    transition: all 200ms ease-out;
  }
  
  .banner-buttons .btn-border:hover,
  .submit-contact .btn-border:hover,
  .projects .btn-border:hover {
    border: 2px solid white;
    background-color: white;
  }
  
  .about {
    padding: 3% 0;
    background: linear-gradient(
      rgba(175, 170, 170, 0.7),
      rgba(173, 164, 164, 0.7)
    ),
      url(/portfolio/static/media/about.13319e5f.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
  }
  
  .about-header h1 {
    font-family: "Source Sans Pro", Arial, sans-serif;
    font-size: 34pt;
  }
  
  .about-header-quote {
    font-family: "Slabo", Helvetica, serif;
    font-style: italic;
    margin-bottom: 50px;
  }
  
  .about-general-description,.about-statistic-description {
    display: flex;
    justify-content: center;
  }
  
  .about-general-description h2,
  .about-statistic-description h2 {
    font-family: "Source Sans Pro", Arial, sans-serif;
    font-size: 24pt;
    margin-bottom: 20px;
  }
  
  .about-general-description p,
  .about-statistic-description p {
    font-family: "Roboto", Helvetica, serif;
    font-size: 12pt;
    margin-bottom: 20px;
  }
  
  .about-statistic-description p {
    margin: 5px 0px;
  }
  .about-option-buttons {
    display: flex;
    justify-content: center;
  }
  .about-option-buttons .btn-black-border{
    background-color: transparent;
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
    color: black;
    font-family: "Roboto", Helvetica, serif;
    font-size: 16pt;
    border-radius: 0;
    border: 2px solid black;
    transition: all 200ms ease-out;
  }
  
  .about-option-buttons .btn-black-border:hover{
    border: 2px solid #464646;
    background-color: #464646;
    color: white;
  }
  
  .summary {
    background: linear-gradient(rgba(250, 250, 250, 0.1), rgba(250, 246, 250, 0.1)),
      url(/portfolio/static/media/project.f5644f94.jpg);
    background-attachment: fixed;
    background-attachment: center center;
    color: white;
    padding: 60px 30px;
  }
  
  .summary .row {
    padding: 50px 6%;
    width: 100%;
  }
  
  .summary h3 {
    font-family: "Source Sans Pro", Arial, sans-serif;
  }
  .summary p {
    font-family: "Roboto", Helvetica, serif;
  }
  
  .projects {
    background-color: #59585d;
    padding: 50px;
    color: white;
  }
  
  .projects-header-quote {
    margin-bottom: 20px;
  }
  .projects h1,
  .projects h3 {
    font-family: "Source Sans Pro", Arial, sans-serif;
  }
  .projects p {
    font-family: "Roboto", Helvetica, serif;
    padding: 0 10px;
  }
  .carousel-container {
    position: relative;
    width: 65%;
    height: 700px;
    margin-left:17%;
  }
.carousel-container .carousel
.slide img {
                height: 100%;
                width: 800px;
            }
 
  
  .projects-header-quote {
    font-style: italic;
  }
  .luvtalk-project, .personal-website-project, .strike-zone-project{
    height: 400px;
    justify-content: center;
  }
  .luvtalk-project h3, .personal-website-project h3, .strike-zone-project h3{
    padding-top: 80px;
  }
  .luvtalk-project {
    background: linear-gradient(rgba(250, 250, 250, 0.1), rgba(250, 246, 250, 0.1)),
      url(/portfolio/static/media/project.f5644f94.jpg);
    background-position: center center;
  }
  .personal-website-project {
    background: linear-gradient(rgba(250, 250, 250, 0.1), rgba(250, 246, 250, 0.1)),
      url(/portfolio/static/media/project.f5644f94.jpg);
    background-position: right center;
  }

  .strike-zone-project {
    background: linear-gradient(rgba(250, 250, 250, 0.1), rgba(250, 246, 250, 0.1)),
      url(/portfolio/static/media/project.f5644f94.jpg);
    background-position: center right;
  }
  
  .contact {
    background: linear-gradient(rgba(10, 10, 10, 0.5), rgba(10, 10, 10, 0.5)), url(/portfolio/static/media/project.f5644f94.jpg);
    color: white;
    padding: 100px;
    background-size: cover;
  }
  
  .contact p {
    font-family: "Roboto", Helvetica, serif;
  }
  .contact-header {
    margin: 80px;
  }
  .contact-footer {
    padding: 10px;
    margin-top: 200px;
  }
  .contact-footer p{
    font-size: 50px;
    font-family: cursive;
  }
  .cantact-ways {
    text-align: center;
  }
  .contact-header-quote {
    display: flex;
    justify-content: center;
    margin: 5px;
  }
  .contact-header-quote img {
    padding: 3px;
    margin-right: 15px;
  }
  .contact-header-quote h3 {
    margin: 5px 0;
  }
  
  
  
